<template>
  <div class="addressBox">
    <div class="address" @click="changeAddress">{{this.currentCity.name}}</div>
    <van-search
      v-model="value"
      placeholder="输入门店或区域名称"
      show-action
      shape="round"
      @search="onSearch"
    >
      <div slot="action" @click="onSearch">搜索</div>
    </van-search>
    <van-list
      style="margin-top:.88rem;"
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <!-- <van-cell v-for="item in list" :key="item" :title="item"/> -->
      <div class="addressItem" v-for="item in store" :key="item.id" @click="storeClick(item)">
        <img :src="item.url" alt>
        <div>
          <div class="storeName">{{item.name}}</div>
          <div class="storeAddress">{{item.address}}</div>
        </div>
      </div>
    </van-list>
  </div>
</template>

<script>
import { Search, Toast, List } from "vant";
import { mapState, mapMutations } from "vuex";
import { log } from "util";
export default {
  name: "choosingStore",
  components: {
    [Search.name]: Search,
    [Toast.name]: Toast,
    [List.name]: List
  },
  data() {
    return {
      value: "",
      info: {},
      store: [        
      ],
      loading: false,
      finished: false
    };
  },
  created() {
    // let thisCity={name:'北京',id:6,spell:'beijing'};
    // this.changeCity(thisCity)
    // console.log(
    //   this.currentCity,
    //   this.currentCity.name,
    //   this.currentCity.id,
    //   "当前选中位置"
    // );

    let urls = window.location.href;
    console.log(urls, "url");
    alert(urls)

    let _this = this;
    this.$http.get("/user/test2", { params: { url: urls } }).then(res => {
      let { code: code, data: data, msg: msg } = res.data;
      this.info.appId = data.appId;
      this.info.timestamp = data.timestamp;
      this.info.nonceStr = data.nonceStr;
      this.info.signature = data.signature;
      this.info.rawString = data.rawString;
      console.log(this.info, wx, "-------------------");

      wx.config({
        debug: true,
        appId: _this.info.appId,
        timestamp: _this.info.timestamp+'',
        nonceStr: _this.info.nonceStr,
        rawString:_this.info.rawString,
        signature: _this.info.signature,
        jsApiList: [
          // 所有要调用的 API 都要加到这个列表中
          "getLocation"
        ]
      });
      wx.ready(function() {
        wx.checkJsApi({
          jsApiList: ["getLocation"],
          success: function(res) {
            console.log(res,'checkJsApi');
            // alert(JSON.stringify(res));
            // alert(JSON.stringify(res.checkResult.getLocation));
            if (res.checkResult.getLocation == false) {
              alert(
                "你的微信版本太低，不支持微信JS接口，请升级到最新的微信版本！"
              );
              return;
            }
          }
        });
        wx.error(function(res) {
          console.log(res,"接口调取失败",_this.info);
          // alert(_this.info.appId);
          // alert(_this.info.timestamp);
          // alert(_this.info.nonceStr);
          // alert(_this.info.signature);
          // alert("接口调取失败");
        });
        wx.getLocation({
          success: function(res) {
            alert(JSON.stringify(res));
            console.log(res,'用户位置');
          },
          cancel: function(res) {
            alert("用户拒绝授权获取地理位置");
          }
        });
      });
    });
  },
  computed: {
    ...mapState({
      currentCity: "city",
      currentStore:"store"
    })
  },
  methods: {
    ...mapMutations(["changeCity",'changeStore']),
    onSearch() {
      var keyword = this.value;
      Toast(this.currentCity.id + "" + this.currentCity.name + "" + keyword);
    },
    changeAddress() {
      //选择省市
      this.$router.push("/city");
    },
    onLoad() {
      // 异步更新数据
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.store.push({
            url: "/images/pay_background@2x.png",
            name: "青年城店"+(i+1),
            id: i+1,
            address: "红军营路"+(i+1)+"号"
          });
        }
        // 加载状态结束
        this.loading = false;
        // 数据全部加载完成
        if (this.store.length >= 40) {
          this.finished = true;
        }
      }, 3000);
    },
    storeClick(item){
      console.log('选择门店',item);
      this.changeStore(item);
      this.$router.push("/payment");
    }
  },
  mounted() {}
};
</script>
<style scope  lang="less">
.addressBox {
  overflow: hidden;
  .address {
    width: 1rem;
    height: 0.88rem;
    font-size: 0.3rem;
    line-height: 0.88rem;
    background: #df0a20;
    position: fixed;
    left: 0;
    text-align: center;
    color: #fff;
  }
  .addressItem {
    height: 2rem;
    overflow: hidden;
    margin: 0 0.2rem;
    border-top: 0.01rem solid #ccc;
    font-size: 0.36rem;
    img {
      margin-top: 0.14rem;
      float: left;
      width: 1.6rem;
      height: 1.6rem;
    }
    .storeName {
      margin-top: 0.1rem;
    }
    & > div > div {
      text-indent: 0.5em;
      height: 0.6rem;
      line-height: 0.6rem;
    }
  }
}

.van-search {
  position: fixed;
  width: 6.5rem;
  right: 0;
  padding: 0.1rem 0.32rem;
  z-index: 99999;
  background: #df0a20 !important;
}
.van-search__action {
  color: #fff;
}
.van-search__action:active {
  background: #df0a20 !important;
}
body {
  margin: 0 !important;
}
</style>

